import { h, Component } from 'preact';
import Carousel from 'nuka-carousel';
import style from './home';

export default class Home extends Component {
	/*

	Project management: https://www.pexels.com/photo/schedule-planning-startup-launching-7376/

	*/

	render() {
		return (
			<div class={style.home}>
				<div class={style.slides}>
					<Carousel speed={300}
						wrapAround={true} 
						autoplay={true}
						autoplayInterval={5000}>
						<div class={style.augment}>Augment your existing development team</div>
						<div class={style.endToEnd}>End to end project develoment</div>
						<div class={style.projectManagement}>Project management services</div>
						<div class={style.consulting}>Consulting services and mentoring</div>
					</Carousel>
				</div>
				<div class={style.blurb}>
					<div class={style.blurbtext}>
						<h2>How can we help?</h2>
						<p>Codingscape is a full service software consulting and development company - were here to help you solve your software goals. Were trustworthy people ready to lend a hand on a problem or take a software project from start to finish.</p>
					</div>
					<div class={style.blurbimage}>
						<img src="assets/images/pair-programming.jpg" />
					</div>
				</div>
				<div class="jumbo">
					Helping you reach your goals. 
				</div>
				<div class={style.blurb}>
					<div class={style.blurbimage}>
					<img src="assets/images/charting.jpg" />
					</div>
					<div class={style.blurbtext}>
						<p>Learn more about the types of projects we work on and the services we offer on our capabilities page! Or contact us if you want to discuss a project in depth - were always happy to hop on the phone and talk through a potential project. Just ask!</p>
					</div>
				</div>
				<div class="jumbo">
					The reward for work well done is the<br /> 
					opportunity to do more.
					<cite>Jonas Salk</cite>
				</div>
			</div>
		);
	}
}



